.header {
  margin: 0 auto 0;
  max-width: 1600px;
  width: 99.5%;
  min-height: 100px;
  display: flex;
  justify-content: space-between; 
  align-items: center;
  background: linear-gradient(180deg, #FFFFFF 0%, #F1F1F1 100%);
}

.header__img-container {
  margin-left: 15px;
  width: 112px;
  display: flex;
  justify-content: space-between;
}

.header__face-img {
  background-image: url('../../images/faceboock.svg');
  background-size: cover;
  width: 27px;
  height: 27px;
}

.header__inst-img {
  background-image: url('../../images/insta.svg');
  background-size: cover;
  width: 27px;
  height: 27px;
}

.header__twit-img {
  background-image: url('../../images/twiter.svg');
  background-size: cover;
  width: 27px;
  height: 27px;
}

.header__link {
  width: 27px;
  height: 27px;
}

.header__title {
  margin: 0 80px 0 0;
  padding: 0;
  font-style: normal;
  font-weight: 700;
  font-size: 32px;
  color: #24B4E6;
}

.header__menu {
  background-image: url('../../images/header-menu.svg');
  background-size: cover;
  width: 40px;
  height: 27px;
  margin-right: 15px;
}

.header__link:hover {
  opacity: 0.6;
  transition: opacity 0.5s ease 0s;
}

@media screen and (max-width: 1280px) {
  .header {
    max-width: 900px;
  }
  .header__title {
    font-size: 27px;
    margin-right: 40px;
  }

  .header__face-img {
    width: 24px;
    height: 24px;
  }
  
  .header__inst-img {
    width: 24px;
    height: 24px;
  }
  
  .header__twit-img {
    width: 24px;
    height: 24px;
  }

  .header__menu {
    height: 24px;
  }
}

@media screen and (max-width: 900px) {
  .header {
    max-width: 768px;
  }
  .header__title {
    margin-right: 10px;
  }
}

@media screen and (max-width: 640px) {
  .header {
    max-width: 480px;
  }

  .header__title {
    font-size: 17px;
    margin-right: 10px;
  }
}

@media screen and (max-width: 480px) {


  .header__title {
    font-size: 15px;
  }
}